export const urlParams = (offset, limit, search) => {
  let params = new URLSearchParams();
  let value = "";
  params.append("offset", offset);
  params.append("limit", limit);
  for (let key in search) {
    if (search[key].length > 0) {
      // value.append(key, search[key].toString())
      value += `${key}=${search[key]}`;
    }
  }

  params.append("search", `${value}`);
  return `${params}`;
};

//     
export const tHeads = [
  { Header: "", accessor: "fio" },
  { Header: "ID ", accessor: "user_id" },
  { Header: " ", accessor: "template" },
  {
    Header: "",
    accessor: "created_at",
    Cell: ({ cell: { value } }) => {
      const date = new Date(value);
      const year = date.getFullYear();
      const mounth = ("0" + date.getMonth() + 1).slice(-2);
      const day = ("0" + date.getDay()).slice(-2);
      return <>{`${day}.${mounth}.${year}`}</>;
    },
  },
  { Header: "", accessor: "bonus" },
  { Header: "", accessor: "discount" },
  { Header: " ", accessor: "loyalty_level" },
  { Header: "   ", accessor: "summ" },
  { Header: "  ", accessor: "summ_all" },
  { Header: " ", accessor: "date_last" },
  { Header: "  ", accessor: "visits" },
  { Header: " ", accessor: "visits_all" },
  {
    Header: " PUSH",
      Cell: <i className="bi bi-send" style={{ margin: "10px" }}></i>
  },
];

export const tHeadsModify = (tHeads) => {
  return tHeads.map((item) => {
    return {
      Header: item.Header,
      colums: [item],
    };
  });
};
